:root {
	--color_primary: #3e4d66;
	--color_primary_80: #657184;
	--color_primary_50: #9fa6b3;
	--color_primary_30: #c5c9d1;
	--color_secondary: #5379b8;
	--color_bubblegum: #f82969;
	--color_bubblegum_alt: #ff5f9e;
	--color_franklin: #28be84;
	--color_franklin_alt: #23d792;

	--color_accent: var(--color_bubblegum);;
	--color_accent_alt: var(--color_bubblegum_alt);;

	--color_error: #ff6b00;

	--color_text: #4a4a4a;

	--color_background: #f6f6f6;
	--color_background_lighter: #f9f9fa;
	--color_page: #fff;
}

* {
	outline: 0;
	margin: 0;
	padding: 0;
	position: relative;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

html {
	font-size: 16px;
}

body {
	font-family: "Roboto", sans-serif;
	font-size: 1rem;
	line-height: 1;
	color: #4a4a4a;
	color: var(--color_text);
	word-wrap: break-word;
	background-color: #f6f6f6;
	background-color: var(--color_background);
}
.-state_statistics {
	--color_accent: var(--color_franklin);
	--color_accent_alt: var(--color_franklin_alt);
}
/*
font-weight: 300            Roboto-Light
font-weight: 400    normal  Roboto-Regular
font-weight: 500            Roboto-Medium
font-weight: 600
font-weight: 700    bold    Roboto-Bold
*/

a.-odd,
a.-odd:hover {color: inherit}
a.-odd:hover,
a.-odd:focus {text-decoration: none}

.-align_left {text-align: left}
.-align_right {text-align: right}

.-align_left > img {margin-right: auto}
.-align_right > img {margin-left: auto}
.-align_center > img {margin: 0 auto}

.-centering,
.-align_center {text-align: center}

.-centering:before,
.-valign_center:before {
	content: '';
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.-centering > *,
.-valign_center > * {
	display: inline-block !important;
	vertical-align: middle !important;
}

.-clear {clear: both}

.-cf:before,
.-cf:after {
	content: "";
	display: table;
}
.-cf:after {clear: both}

.tab,
.stamp,
.action,
.button,
.counter,
.form_radio + .form_label,
.form_checkbox + .form_label,
.-unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: -moz-none;
	-ms-user-select: none;
	user-select: none;
}

.-hidden {display: none !important}

.fa_icon:before {
	display: inline;
	font: normal normal normal 1em/1 FontAwesome;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transform: translate(0, 0);
}

.fa_icon:not(.-empty):not(:empty):before {
	font-size: 1em;
	margin-right: .5em;
}

img[src=''],
img:not([src]) {display: none !important;}

hr[title]:before {
	content: attr(title);
	position: absolute;
	left: 0;
	top: -.5em;
	padding: 0 12px;
	font-weight: 500;
	background-color: #fff;
}

.img_holder > img {
	display: block;
	max-width: 100%;
	max-height: 100%;
}

.img_holder > .placeholder {
	opacity: 0;
	visibility: hidden;
}

.link {
	color: inherit;
	text-decoration: none !important;
}
[data-page],
.link {cursor: pointer}
[data-page]:not(.button):hover,
[data-page]:not(.button):focus,
.link:not(.button):hover,
.link:not(.button):focus {
	color: #f82969;
	color: var(--color_accent);
}

.-state_statistics [data-page]:not(.button):hover {color: #28be84}

/* Collapsing wrapper */

.collapsing_wrapper {
	height: 0;
	background-color: #f9f9fa;
	background-color: var(--color_background_lighter);
	box-shadow: inset 0 5px 3px -3px #ccc, inset 0 -5px 3px -3px #ccc;
	overflow: hidden;
	-webkit-transition: height .3s ease;
	-moz-transition: height .3s ease;
	-o-transition: height .3s ease;
	transition: height .3s ease;
}

.collapsing_wrapper.-fading:after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100%;
	background-image: -webkit-linear-gradient(top, hsla(0,0%,100%,0), #fff);
	background-image: -moz-linear-gradient(top, hsla(0,0%,100%,0), #fff);
	background-image: -ms-linear-gradient(top, hsla(0,0%,100%,0), #fff);
	background-image: -o-linear-gradient(top, hsla(0,0%,100%,0), #fff);
	background-image: linear-gradient(to bottom, hsla(0,0%,100%,0), #fff);
	-webkit-transition: height .3s ease;
	-moz-transition: height .3s ease;
	-o-transition: height .3s ease;
	transition: height .3s ease;
	cursor: pointer;
}

.collapsing_wrapper.-fading.-opened:after {height: 0}

.collapsing_wrapper.-fading {
	background-color: transparent;
	box-shadow: none;
}

:not(.-fading) > .collapsing_content {border: 1px solid transparent}

.collapsing_button {
	width: 100%;
	height: 2em;
	font-size: 0.88em;
	border: 0;
	background-color: transparent;
	text-align: center;
	-webkit-transition: -webkit-transform .3s ease;
	-moz-transition: -moz-transform .3s ease;
	-o-transition: -o-transform .3s ease;
	transition: transform .3s ease;
}
.collapsing_button:hover,
.collapsing_wrapper.-fading:not(.-opened):hover + .collapsing_button {color: #f82969; color: var(--color_bubblegum);}

.collapsing_wrapper.-fading.-opened + .collapsing_button {
	-webkit-transform: rotateZ(180deg);
	-moz-transform: rotateZ(180deg);
	-ms-transform: rotateZ(180deg);
	-o-transform: rotateZ(180deg);
	transform: rotateZ(180deg);
}

/* /Collapsing wrapper */
/* Typography */

h1, h2, h3, h4 {font-weight: 500}

h1 {
	font-size: 2.2rem;
	margin: 1.5rem 0 .8rem;
}

h2 {
	font-size: 1.9rem;
	margin: 1.8rem 0 .6rem;
}

h3 {
	font-size: 1.6rem;
	margin: 1.4rem 0 .5rem;
}
h4 {
	font-size: 1.1rem;
	margin: 2.5em 0 .8em;
}

p {margin-bottom: .4rem}

label {font-weight: inherit}



p.-color_default,
p.-color_neutral,
small.-color_default,
small.-color_neutral,
label.-color_default,
label.-color_neutral {color: #fff; color: var(--color_page);}

p.-color_primary,
