:not(.-state_statistics) p.-color_accent_alt,
:not(.-state_statistics) small.-color_accent_alt,
:not(.-state_statistics) label.-color_accent_alt {color: #ff5f9e; color: var(--color_accent_alt);}

p.-color_franklin,
small.-color_franklin,
label.-color_franklin,
.-state_statistics p.-color_accent,
.-state_statistics small.-color_accent,
.-state_statistics label.-color_accent {color: #28be84}

p.-color_franklin_alt,
small.-color_franklin_alt,
label.-color_franklin_alt,
.-state_statistics p.-color_accent_alt,
.-state_statistics small.-color_accent_alt,
.-state_statistics label.-color_accent_alt {color: #23d792}

p.-color_marginal,
