.form_select {
	-moz-appearance: none;
	background-color: #fff;
	background-color: var(--color_page);
	border: 1px solid #c5c9d1;
	border: 1px solid var(--color_primary_30);
	box-shadow: 0 1px 2px #d9d9d9;
