	content: "\f0d7";
	position: absolute;
	right: 0;
	top: 0;
	margin: .5rem;
	font-family: FontAwesome;
}

/* Form Select2 */

.form_select2 {
	display: block;
	width: 100%;
	border-radius: 2px;
	font-size: .9rem;
}
.form_select2 > .select2-choice {
	height: 2.2rem;
	padding: 0;
