	border: 1px solid var(--color_primary_30);
	border-radius: 2px;
	box-shadow: 0 1px 2px #d9d9d9;
}
.form_select2.select2-dropdown-open > .select2-choice {border-radius: 3px 3px 0 0}

.form_select2.select2-container > .select2-choice > .select2-chosen {
	line-height: 2.2rem;
