/* Form Radio & Checkbox */

.form_unit > input[type="radio"],
.form_unit > input[type="checkbox"] {display: none}

.form_radio + .form_label,
.form_checkbox + .form_label {
	display: block;
	font-size: 0;
	white-space: nowrap;
	cursor: pointer;
}

