	display: inline-block;
	vertical-align: middle;
	width: calc(100% - 20px - 10px);
	margin-left: 10px;
	font-size: .9rem;
	line-height: 1;
	text-align: left;
	white-space: normal;
}

.form_radio + .form_label:before,
.form_checkbox + .form_label:before {
	content: "";
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 20px;
	border: 1px solid #9fa6b3;
	border: 1px solid var(--color_primary_50);
	border-radius: 2px;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.form_radio + .form_label:before {border-radius: 1rem}

.form_checkbox + .form_label:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	display: inline-block;
	width: 13px;
	height: 6px;
	margin: auto 2px;
	border-left: 2px solid transparent;
	border-bottom: 2px solid transparent;
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
	transform-origin: top center;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.form_radio + .form_label:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	display: inline-block;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	margin: auto 5px;
	background-color: transparent;
}

.form_radio:checked + .form_label:before,
.form_radio:not(:disabled) + .form_label:hover:before,
