/*/Forms*/
/*Button*/

.button {
	display: inline-block;
	vertical-align: bottom;
	height: 2.2rem;
	min-width: 2.2rem;
	font-family: inherit;
	font-size: .81em;
	font-weight: 500;
	color: inherit;
	border: 0;
	border-radius: 2px;
	background-color: transparent;
	text-align: center;
	text-transform: uppercase;
	overflow: hidden;
	cursor: pointer;
	word-wrap: normal;
	-webkit-transition: box-shadow .5s ease, background-color .5s ease, color .5s ease;
	-moz-transition: box-shadow .5s ease, background-color .5s ease, color .5s ease;
	-o-transition: box-shadow .5s ease, background-color .5s ease, color .5s ease;
	transition: box-shadow .5s ease, background-color .5s ease, color .5s ease;

	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.button:not(.-empty):not(:empty) {padding: 0 .8em}

.button.-fill {width: 100%}
.button.-size_low {
	height: 1.88rem;
	min-width: 1.88rem;
}
.button.-rounded {
	font-size: 0.68em;
	border-radius: 50px;
}

a.button {line-height: 2.2rem}
a.button.-size_low {line-height: 1.88rem}
a.button,
a.button:hover {text-decoration: none}



.button.-color_primary,
.button.-color_accent,
.button.-color_telegram {color: #fff}

.button.-color_default,
.button.-color_marginal,
.button.-color_neutral {color: #4a4a4a; color: var(--color_text);}

.button.-color_neutral_alt {color: #9fa6b3; color: var(--color_primary_50);}

.button.-color_primary_alt,
.button.-color_neutral_primary,
.button.-color_marginal_primary {color: #3e4d66; color: var(--color_primary);}

.button.-color_accent_alt,
.button.-color_neutral_accent,
.button.-color_marginal_accent {color: #f82969; color: var(--color_accent);}

.-state_statistics .button.-color_accent_alt,
.-state_statistics .button.-color_neutral_accent,
.-state_statistics .button.-color_marginal_accent {color: #28be84}



.button.-color_default,
.button.-color_neutral_alt {background-color: transparent}

.button.-color_primary {background-color: #3e4d66; background-color: var(--color_primary);}
.button.-color_accent {background-color: #f82969; background-color: var(--color_accent);}
.-state_statistics .button.-color_accent {background-color: #28be84}

.button.-color_primary_alt {background-color: rgba(83,121,184,0.24)}
.button.-color_accent_alt {background-color: #ffdeeb}

.button.-color_neutral,
.button.-color_neutral_primary,
.button.-color_neutral_accent {background-color: #fff; background-color: var(--color_page);}

.button.-color_marginal,
.button.-color_marginal_primary,
.button.-color_marginal_accent {background-color: #fff; background-color: var(--color_page);}



.button.-color_neutral,
.button.-color_neutral_primary,
.button.-color_neutral_accent {box-shadow: 0 1px 2px #b3b3b3, 0 0 3px #ccc}


.button.-color_telegram {
	font-size: 13px;
	background-image: radial-gradient(circle at 50% 0, #38b0e3, #1d93d2);
	box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.22);
}



.button:not(:disabled).-color_default:hover,
.button:not(:disabled):not([class*="-color_"]):hover {background-color: #f1f1f1}
.button:not(:disabled)[class*="-color_"]:hover {box-shadow: 0 1px 6px #b3b3b3}

.button:not(:disabled).-color_default:hover,
.button:not(:disabled).-color_marginal:hover,
.button:not(:disabled).-color_accent_alt:hover,
.button:not(:disabled).-color_marginal_accent:hover,
