.button:not(:disabled).-color_marginal_primary:hover,
.button:not(:disabled).-color_marginal_accent:hover {background-color: #ebedef}


.button:disabled,
.button.-status_disabled {
	pointer-events: none;
	cursor: default;
	color: #9fa6b3;
	color: var(--color_primary_50);
	background-color: #f6f6f6;
	background-color: var(--color_background);
	box-shadow: none;
}

/* TODO: Ripple on checkboxes and radios */

.ripple {
	position: absolute;
	display: block;
	border-radius: 100%;
	background-color: hsla(0,0%,100%,.7);
	-webkit-transform: scale(0);
	-moz-transform: scale(0);
	-ms-transform: scale(0);
	-o-transform: scale(0);
	transform: scale(0);
	z-index: -1;
}
.ripple.animate {animation: ripple 0.65s linear;}
@keyframes ripple {
	100% {
		opacity: 0;
		-webkit-transform: scale(2.5);
		-moz-transform: scale(2.5);
		-ms-transform: scale(2.5);
		-o-transform: scale(2.5);
		transform: scale(2.5);
	}
}

.button.-color_primary > .ripple,
.button.-color_accent > .ripple,
.button.-color_default > .ripple,
.button.-color_neutral_alt > .ripple {background-color: #fff}

.button.-color_primary_alt > .ripple,
.button.-color_accent_alt > .ripple,
.button.-color_neutral > .ripple,
.button.-color_marginal > .ripple,
.button.-color_neutral_primary > .ripple,
.button.-color_marginal_primary > .ripple {background-color: #c5c9d1; background-color: var(--color_primary_30)}

.button.-color_neutral_accent > .ripple,
.button.-color_marginal_accent > .ripple {background-color: #ff5f9e; background-color: var(--color_accent_alt)}

.-state_statistics .button.-color_neutral_accent > .ripple,
.-state_statistics .button.-color_marginal_accent > .ripple {background-color: #23d792}

