.dropdown_box_footer {
	padding: 12px;
	font-size: .75em;
	border-top: 2px solid #e5e5e5;
	text-align: right;
}

.dropdown_box_footer > .action + .action {margin-left: 24px}

/* /Dropdown */
/* Tabs */

.tabs_header {
	text-align: left;
}

.tab {
	display: inline-block;
	padding: 8px 1px;
	margin-right: 1em;
	font-size: .9em;
	font-weight: 500;
	text-transform: uppercase;
	border-bottom: 2px solid transparent;
	opacity: .8;
	cursor: pointer;

	-webkit-transition: color .5s ease, border-color .5s ease;
	-moz-transition: color .5s ease, border-color .5s ease;
	-o-transition: color .5s ease, border-color .5s ease;
	transition: color .5s ease, border-color .5s ease;
}

.tab:hover {opacity: 1}

.tab.-active {
	border-color: inherit;
	opacity: 1;
	cursor: default;
}

.-color_primary .tab.-active,
.tab.-color_primary.-active {
	color: #3e4d66;
	color: var(--color_primary);
	border-color: #3e4d66;
	border-color: var(--color_primary);
}

.-color_accent .tab.-active,
.tab.-color_accent.-active {
	color: #f82969;
	color: var(--color_accent);
	border-color: #f82969;
	border-color: var(--color_accent);
}

.-state_statistics .-color_accent .tab.-active,
.-state_statistics .tab.-color_accent.-active {
	color: #28be84;
	border-color: #28be84;
}

.-color_default .tab.-active,
.tab.-color_default.-active {
	color: #fff;
	border-color: #fff;
}

.tab_bodies_wrap {
	margin: 20px 0;
	overflow: hidden;

	-webkit-transition: height .5s ease;
	-moz-transition: height .5s ease;
	-o-transition: height .5s ease;
	transition: height .5s ease;
}

.tab_body {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;

	-webkit-transform: translateX(-100%);
	-moz-transform: translateX(-100%);
	-ms-transform: translateX(-100%);
	-o-transform: translateX(-100%);
	transform: translateX(-100%);

	-webkit-transition: transform .5s ease;
	-moz-transition: transform .5s ease;
	-o-transition: transform .5s ease;
	transition: transform .5s ease;
}
.tab_body.-active {
	-webkit-transform: translateX(0);
	-moz-transform: translateX(0);
	-ms-transform: translateX(0);
	-o-transform: translateX(0);
	transform: translateX(0);
}

.tab_body.-active ~ .tab_body {
	-webkit-transform: translateX(100%);
	-moz-transform: translateX(100%);
	-ms-transform: translateX(100%);
	-o-transform: translateX(100%);
	transform: translateX(100%);
}

/* /Tabs */
/* Loader */

.loader {
	width: 100px;
	height: 100px;
	margin: auto;
}

.loader:only-child {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 50px;
	height: 50px;
}

.loader_dot {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 50px;
	height: 50px;
	margin: auto;
	border-radius: 50%;
	background-color: #f82969;
	background-color: var(--color_accent);
	opacity: 0.6;

	-webkit-animation: loader-bounce 2.4s infinite ease-in-out;
	animation: loader-bounce 2.4s infinite ease-in-out;
}

.-state_statistics .loader_dot {background-color: #28be84}

.loader_dot:last-child {
	-webkit-animation-delay: -1.4s;
	animation-delay: -1.4s;
}

@-webkit-keyframes loader-bounce {
	0%, 100% { -webkit-transform: scale(0) }
	50% { -webkit-transform: scale(1) }
}

@keyframes loader-bounce {
	0%, 100% {
		transform: scale(0);
		-webkit-transform: scale(0);
	}
	50% {
		transform: scale(1);
		-webkit-transform: scale(1);
	}
}

/* /Loader */
/* Scoreboard */

.scoreboard_number {
	display: block;
	margin-bottom: 6px;
	font-size: 2.3em;
	font-weight: 700;
}

.scoreboard_dynamic {
	margin: 6px 0 12px;
	font-size: 14px;
	font-weight: 400;
}

.scoreboard_dynamic_icon {
	display: inline-block;
	vertical-align: sub;
	height: 24px;
	font-size: 24px;
	line-height: 14px;
	margin: 0 .1em 0 0;
}

.scoreboard_title {
	font-size: .88em;
	font-weight: 500;
	color: #9fa6b3;
	color: var(--color_primary_50);
}


.scoreboard.-size_big > .scoreboard_number {
	font-size: 4.2em;
	font-weight: bold;
	color: #3e4d66;
	color: var(--color_primary);
}

.scoreboard.-size_big > .scoreboard_title {
	font-size: 1.25em;
	font-weight: 500;
}

/* /Scoreboard */
/* Notification */

#notification {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 40px;
	color: #fff;
	background: #ff5f9e url(../img/notification_pattern.png) repeat-x;
	background: var(--color_bubblegum_alt) url(../img/notification_pattern.png) repeat-x;
	-webkit-transform: translateY(-40px);
	-moz-transform: translateY(-40px);
	-ms-transform: translateY(-40px);
	-o-transform: translateY(-40px);
	transform: translateY(-40px);
	-webkit-transition: transform .5s ease;
	-moz-transition: transform .5s ease;
	-o-transition: transform .5s ease;
	transition: transform .5s ease;
}

#notification.-show {
	-webkit-transform: translateY(60px);
	-moz-transform: translateY(60px);
	-ms-transform: translateY(60px);
	-o-transform: translateY(60px);
	transform: translateY(60px);
}

/* /Notification */
/* Modal */

body.-open_modal {overflow: hidden}
html.-noscroll {
	position: fixed;
	overflow-y: scroll;
	width: 100%;
}

.modal_wrapper {
	position: fixed;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	overflow: auto;
	outline: 0;
	-webkit-overflow-scrolling: touch;
	background-color: hsla(218,24%,32%,.6);
	z-index: 1050;
}

.modal_destroyer {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.modal_unit {
	display: block;
	width: 800px;
	margin: 100px auto;
	background-color: #fff;
	background-color: var(--color_page);
	box-shadow: 0 2px 15px hsla(0,0%,0%,.2);
	border-radius: 2px;
}

.modal_wrapper.-blackened > .modal_unit {
	background-color: transparent;
	box-shadow: none;
}

.modal_wrapper,
.modal_unit {
	-webkit-transition: opacity .2s ease, visibility .2s ease;
	-moz-transition: opacity .2s ease, visibility .2s ease;
	-o-transition: opacity .2s ease, visibility .2s ease;
	transition: opacity .2s ease, visibility .2s ease;
}

.modal_wrapper,
.modal_unit.-faded {
	opacity: 0;
	visibility: hidden;
}

.modal_unit,
body.-open_modal > .modal_wrapper {
	opacity: 1;
	visibility: visible;
}

.modal_header {
	padding: 10px 25px;
	border-bottom: 1px solid #c5c9d1;
	border-bottom: 1px solid var(--color_primary_30);
	text-align: left;
}

.modal_wrapper.-blackened .modal_header {border-bottom: 0}

.modal_header > h4 {
	margin: 0;
	font-size: 1.1rem;
	color: #3e4d66;
	color: var(--color_primary);
}
.-modal_destroyer {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto 4px;
	font-size: 1.8em;
}
.modal_wrapper.-blackened .-modal_destroyer {
	position: absolute;
	bottom: 20px;
	right: -5px;
	color: #fff;
	font-weight: 300;
	background-color: transparent !important;
}

.modal_content {padding: 25px}
.modal_content.-no_padding {padding: 0}

.modal_divider {
	margin-bottom: 15px;
	font-size: .88em;
	font-weight: 500;
	color: #9fa6b3;
	color: var(--color_primary_50);
	text-transform: uppercase;
}

.modal_footer {
	padding: 15px 25px;
	text-align: right;
	border-top: 1px solid #c5c9d1;
	border-top: 1px solid var(--color_primary_30);
}

.modal_footer > .button + .button {margin-left: .5em}


/* /Modal */
/* Calendar */

.calendar_wrapper {
	min-height: -webkit-calc(2.4em * 7 + 3em); /* To make calendar with similar height either with 5 or 6 weeks */
	min-height: calc((2.4em * 7) + 3em); /* 2.4em - cell height, 5px - cell spacing and 3em for header height */
	cursor: default;
}

.calendar_wrapper {
	display: inline-block;
	font-size: 0.8rem;
}

.calendar_header {
	padding: 0 7px;
	font-weight: 500;
	color: #f82969;
	color: var(--color_accent);
	text-align: center;
}

.-state_statistics .calendar_header {color: #28be84}

.calendar_prev_btn,
.calendar_next_btn {
	display: inline-block;
	vertical-align: middle;
	width: 2em;
	height: 2em;
	font-size: 1em;
	color: inherit;
	border: 0;
	border-radius: 2px;
	background-color: transparent;
	cursor: pointer;
}

.calendar_month_name {
	display: inline-block;
	vertical-align: middle;
	width: -webkit-calc(100% - 5em);
	width: calc(100% - 5em);
	padding: .5em;
	margin: 0 .5em;
	font-size: 1.1em;
	border-radius: 2px;
	text-transform: uppercase;

}
.-month_selection .calendar_month_name {cursor: pointer}



.calendar_month {
	border-collapse: collapse;
	border-spacing: 0;
}


.calendar_cell {
	width: 2.4em;
	height: 2.4em;
	text-align: center;
}

.calendar_cell.-active {
