	background-color: #657184;
	background-color: var(--color_primary_80);
}
.-weekday_selection .calendar_weekday {cursor: pointer}

.calendar_day:not(.-disabled) {cursor: pointer}
.calendar_day.not_this_month,
.calendar_day.-disabled,
.event_calendar_day.not_this_month,
.event_calendar_day.-disabled {opacity: .5}

/* /Calendar */
/* DatePicker */

.date_picker {
	position: absolute;
	right: 0;
	min-width: 260px;
	padding: 20px;
	font-size: 1rem;
	background-color: #fff;
	background-color: var(--color_page);
	box-shadow: 0 2px 6px hsla(0,0%,0%,.28);
	z-index: 10;
}

/* /DatePicker */
/* Avatar */

.avatar {
	display: inline-block;
	vertical-align: middle;
	width: 70px;
	height: 70px;
	background-size: contain;
}

.avatar > img {
	max-width: 100%;
	max-height: 100%;
}

.avatar.-rounded {
	border-radius: 50%;
	overflow: hidden;
	-webkit-transform: translate3d(0,0,0);
	-moz-transform: translate3d(0,0,0);
	-ms-transform: translate3d(0,0,0);
	-o-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
}
.avatar.-bordered {border: 2px solid #fff}
.avatar.-shadowed {box-shadow: 0 1px 4px rgba(0,0,0,0.16)}

.avatar.-size_small,
.avatar.-size_30x30,
.-size_small > .avatar,
.-size_30x30 > .avatar {
	width: 30px;
	height: 30px;
}
.avatar.-size_40x40,
.-size_40x40 > .avatar {
	width: 40px;
	height: 40px;
}
.avatar.-size_50x50,
.-size_50x50 > .avatar {
	width: 50px;
	height: 50px;
}
.avatar.-size_55x55,
.-size_55x55 > .avatar {
	width: 55px;
	height: 55px;
}
.avatar.-size_100x100,
.-size_100x100 > .avatar {
	width: 100px;
	height: 100px;
}

.avatar.-size_40x40.-bordered {
	border: 1px solid #fff;
	box-shadow: 0 1px 4px 0 #bdbdbd;
}

/* /Avatar */
/* Avatars Collection */

.avatars_collection {
	display: inline-block;
	vertical-align: middle;
	font-size: 0;
}
.avatars_collection > .avatar + .avatar {margin-left: -6px}

.avatars_collection.-rounded > .avatar {
	border-radius: 50%;
	overflow: hidden;
}
.avatars_collection.-bordered > .avatar {border: 2px solid #fff}

.avatars_collection.-trimmed {
	width: 0;
	overflow: hidden;
	white-space: nowrap;
	border-radius: 50px;
	-webkit-transition: width .5s ease;
	-moz-transition: width .5s ease;
	-o-transition: width .5s ease;
	transition: width .5s ease;

	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.avatars_collection.-trimmed > .avatar:last-child {
	-webkit-transition: opacity .5s ease, left .5s ease;
	-moz-transition: opacity .5s ease, left .5s ease;
	-o-transition: opacity .5s ease, left .5s ease;
	transition: opacity .5s ease, left .5s ease;
}

.avatars_collection.-trimmed > .avatar:first-child {
	-webkit-transition: opacity .5s ease, margin-left .5s ease, left .5s ease;
	-moz-transition: opacity .5s ease, margin-left .5s ease, left .5s ease;
	-o-transition: opacity .5s ease, margin-left .5s ease, left .5s ease;
	transition: opacity .5s ease, margin-left .5s ease, left .5s ease;
}
.avatars_collection.-trimmed > .avatar:last-child,
.avatars_collection.-trimmed > .avatar:first-child {
	left: 0;
	opacity: 0;
}

.avatars_collection.-trimmed:not(.-shift) > .avatar:last-child,
.avatars_collection.-trimmed.-subscribed > .avatar:first-child {opacity: 1}


.avatars_collection.-trimmed:not(.-subscribed) > .avatar:first-child {left: -6px}
.avatars_collection.-trimmed.-shift > .avatar:last-child {left: 6px}
.avatars_collection.-trimmed.-size_small > .avatar:first-child,
.avatars_collection.-trimmed.-size_30x30 > .avatar:first-child {margin-left: -24px}
.avatars_collection.-trimmed.-size_40x40 > .avatar:first-child {margin-left: -34px}
.avatars_collection.-trimmed.-size_50x50 > .avatar:first-child {margin-left: -44px}

.avatars_collection.-trimmed.-subscribed > .avatar:first-child {
	margin-left: 0;
	left: 0;
}

/* /Avatars Collection */
/* Avatar Block */

.avatar_block {
	display: block;
	font-size: 0;
